export default {
	data() {
		return {
			reviewList:[],
			flag:true,
			pageIndex:0,
			isOver:false
		}
	},
	onShow() {
		this.getToken()
	},
	onReachBottom() {
		var that = this
		if(this.reviewList.length < this.pageIndex*5){
			this.isOver = true
		}
		console.log(this.reviewList.length)
		this.pageIndex++
		this.getreviewList()
	},
	methods: {
		//获取token
		getToken(){
			var that = this
			uni.getStorage({
				key:'token',	
				success: function (res) {
				that.token = res.data.token
				console.log(that.token)
				that.getreviewList()
				},
			})
		},
		//撤销预约时间
		undo(id){
			var that = this;				
			console.log(id)
			console.log(that.token)
			uni.request({
				url:'http://112.74.86.165:8082/revocation',
				method:'PUT',
				data:{
					meetingId:id,
				},
				
				header:{
					Authorization:that.token,
					'content-type':"application/x-www-form-urlencoded"
				},
				success(res) {
					uni.navigateTo({
						url:'../../results/sUndo'
					})
				}
			})
			
		},
		//修改预约
		update(time,id,meetingName,meetingRoomId,name,phone){
			uni.navigateTo({
				url:'../update/update?id='+id+'&'+'meetingName='+meetingName+'&'+'meetingRoomId='+meetingRoomId+'&'+'phone='+phone+'&'+'name='+name+'&'+'time='+time
			})
		},
		//获取审核中列表
		getreviewList(){
			var that = this;
			uni.request({
				url:'http://112.74.86.165:8082/audit',
				method:'GET',
				data:{
					pagination:this.pageIndex,
					size:'5'
				},
				header:{
					Authorization:that.token
				},
				success(res) {
					console.log(res)
					that.reviewList = [...that.reviewList,...res.data.data.list]
					if(that.reviewList.length == 0){
						that.flag = false
					}
				}
			})
		}
	}
}
